.footer {
  background-color: $color-bg-footer;
}

.footer__nav {
  display: flex;
  justify-content: space-between;
  align-items: start;
  padding-top: 40px;
  padding-bottom: 40px;

  @include vp-1199 {
    padding-top: 45px;
    padding-bottom: 45px;
  }

  @include vp-767 {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: repeat(4, auto);
    column-gap: 30px;
    row-gap: 11px;
    grid-auto-flow: column;
    padding-bottom: 52px;
  }
}

.footer__logo {
  margin-top: 10px;

  @include vp-1199 {
    margin-top: 3px;
  }

  @include vp-767 {
    margin-top: 5px;
  }

  &:hover {
    opacity: 0.6;
  }

  &:focus {
    opacity: 0.6;
    outline: none;
  }

  &:active {
    opacity: 0.3;
  }
}

.footer__social {
  grid-row: 4 / 5;
  grid-column: 1;

  @include vp-767 {
    margin-top: 20px;
  }
}
