.form {
  background-color: $color-bg-dark;
  grid-column: 1 / 2;
  display: grid;
  grid-template-columns: 1fr 580px;

  @include vp-1199 {
    grid-template-columns: 1fr 500px 1fr;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
  }
}

.form__container {
  padding-top: 55px;
  padding-bottom: 65px;
  grid-column: 2 / 3;
  justify-self: end;
  padding-right: 170px;

  @include vp-1199 {
    padding-top: 63px;
    padding-bottom: 70px;
    padding-right: 25px;
    grid-column: 2 / 3;
    justify-self: stretch;
  }

  @include vp-767 {
    padding: 65px 15px 67px;
    grid-column: 1 / 2;
  }
}

.form__wrapper {
  width: 410px;

  @include vp-1199 {
    width: 100%;
  }
}

.form h2 {
  @include text-alumnisans-54-85-800;
  color: $color-text-white;
  margin-top: 0;
  margin-bottom: 10px;

  @include vp-1199 {
    @include text-alumnisans-42-50-700;
    margin-bottom: 27px;
  }

  @include vp-767 {
    @include text-alumnisans-32-38-600;
    margin-bottom: 30px;
  }
}

.form__wrapper form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 30px;
  row-gap: 25px;

  @include vp-1199 {
    column-gap: 60px;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
    row-gap: 23px;
  }
}

.form__wrapper input {
  @include text-roboto-14-21-400;
  color: $color-form-primary;
  width: 100%;
  background-color: transparent;
  box-shadow: none;
  border: none;
  padding-top: 8px;
  padding-bottom: 6px;
  border-bottom: 1px solid $color-input-border-50;

  &:focus::-webkit-input-placeholder {
    color: transparent;
  }

  &:focus::-moz-placeholder {
    color: transparent;
  }

  &:focus:-moz-placeholder {
    color: transparent;
  }

  &:focus:-ms-input-placeholder {
    color: transparent;
  }

  &:active::-webkit-input-placeholder {
    color: transparent;
  }

  &:active::-moz-placeholder {
    color: transparent;
  }

  &:active:-moz-placeholder {
    color: transparent;
  }

  &:active:-ms-input-placeholder {
    color: transparent;
  }

  &:hover {
    border-bottom: 1px solid $color-input-border-100;
  }

  &:focus {
    outline: none;
    padding-top: 7px;
    padding-left: 12px;
    border: 1px solid $color-input-border-100;
  }

  &:active {
    outline: none;
    padding-top: 7px;
    padding-left: 12px;
    border: 1px solid $color-input-border-100;
  }

  &:disabled {
    pointer-events: none;
    opacity: 0.4;
  }

  &:disabled:hover {
    border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  }

  &:disabled:active {
    border: none;
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
    padding-left: 2px;
    padding-top: 8px;

    &::-webkit-input-placeholder {
      color: $color-text-white;
    }
  }

  @include vp-767 {
    padding-bottom: 4px;
  }

  &::placeholder {
    color: $color-form-primary;
    margin-left: -10px;
  }
}

.form__wrapper .is-invalid input {
  border: none;
  border-bottom: 1px solid $color-form-error;
}


.form__input .is-invalid {
  @include text-roboto-14-21-400;
  color: $color-form-error;
}

.form__wrapper button {
  grid-column: 1 / 3;
  letter-spacing: 1.6px;
  padding-top: 18px;
  padding-bottom: 18px;

  @include vp-1199 {
    padding-bottom: 14px;
  }

  @include vp-767 {
    margin-top: 15px;
    padding-top: 18px;
    padding-bottom: 18px;
  }
}
