    grid-template-columns: 1fr 500px 1fr;
  }
}
@media (max-width: 767px) {
  .form {
    grid-template-columns: 1fr;
  }
}

.form__container {
  padding-top: 55px;
  padding-bottom: 65px;
  grid-column: 2/3;
  justify-self: end;
  padding-right: 170px;
}
@media (max-width: 1199px) {
  .form__container {
    padding-top: 63px;
    padding-bottom: 70px;
    padding-right: 25px;
    grid-column: 2/3;
    justify-self: stretch;
  }
}
@media (max-width: 767px) {
  .form__container {
    padding: 65px 15px 67px;
    grid-column: 1/2;
  }
}

.form__wrapper {
  width: 410px;
}
@media (max-width: 1199px) {
  .form__wrapper {
    width: 100%;
  }
}

.form h2 {
  font-family: "Alumnisans", "Arial", sans-serif;
  font-size: 54px;
  line-height: 85px;
  font-weight: 800;
  letter-spacing: 2.2px;
  color: #ffffff;
  margin-top: 0;
  margin-bottom: 10px;
}
@media (max-width: 1199px) {
  .form h2 {
    font-size: 42px;
    line-height: 50px;
    letter-spacing: 1.26px;
  }
}
@media (max-width: 767px) {
  .form h2 {
    font-size: 32px;
    line-height: 38px;
    letter-spacing: 0.96px;
    font-weight: 600;
  }
}
@media (max-width: 1199px) {
  .form h2 {
    font-family: "Alumnisans", "Arial", sans-serif;
    font-size: 42px;
    line-height: 50px;
    font-weight: 700;
    letter-spacing: 1.26px;
    margin-bottom: 27px;
  }
}
@media (max-width: 767px) {
  .form h2 {
    font-family: "Alumnisans", "Arial", sans-serif;
    font-size: 32px;
    line-height: 38px;
    font-weight: 600;
    letter-spacing: 0.96px;
    margin-bottom: 30px;
  }
}

.form__wrapper form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 30px;
  row-gap: 25px;
}
@media (max-width: 1199px) {
  .form__wrapper form {
    column-gap: 60px;
  }
}
@media (max-width: 767px) {
  .form__wrapper form {
    display: flex;
    flex-direction: column;
    row-gap: 23px;
  }
}

.form__wrapper input {
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  color: #ffffff;
  width: 100%;
  background-color: transparent;
  box-shadow: none;
  border: none;
  padding-top: 8px;
  padding-bottom: 6px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}
.form__wrapper input:focus::-webkit-input-placeholder {
  color: transparent;
}
.form__wrapper input:focus::-moz-placeholder {
  color: transparent;
}
.form__wrapper input:focus:-moz-placeholder {
  color: transparent;
}
.form__wrapper input:focus:-ms-input-placeholder {
  color: transparent;
}
.form__wrapper input:active::-webkit-input-placeholder {
  color: transparent;
}
.form__wrapper input:active::-moz-placeholder {
  color: transparent;
}
.form__wrapper input:active:-moz-placeholder {
  color: transparent;
}
.form__wrapper input:active:-ms-input-placeholder {
  color: transparent;
}
.form__wrapper input:hover {
  border-bottom: 1px solid rgb(255, 255, 255);
}
.form__wrapper input:focus {
  outline: none;
  padding-top: 7px;
  padding-left: 12px;
  border: 1px solid rgb(255, 255, 255);
}
.form__wrapper input:active {
  outline: none;
  padding-top: 7px;
  padding-left: 12px;
  border: 1px solid rgb(255, 255, 255);
}
.form__wrapper input:disabled {
  pointer-events: none;
  opacity: 0.4;
}
.form__wrapper input:disabled:hover {
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
}
.form__wrapper input:disabled:active {
  border: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  padding-left: 2px;
  padding-top: 8px;
}
.form__wrapper input:disabled:active::-webkit-input-placeholder {
  color: #ffffff;
}
@media (max-width: 767px) {
  .form__wrapper input {
    padding-bottom: 4px;
  }
}
.form__wrapper input::placeholder {
  color: #ffffff;
  margin-left: -10px;
}

.form__wrapper .is-invalid input {
  border: none;
  border-bottom: 1px solid #ff121f;
}

.form__input .is-invalid {
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  color: #ff121f;
}

.form__wrapper button {
  grid-column: 1/3;
  letter-spacing: 1.6px;
  padding-top: 18px;
  padding-bottom: 18px;
}
@media (max-width: 1199px) {
  .form__wrapper button {
    padding-bottom: 14px;
  }
}
@media (max-width: 767px) {
  .form__wrapper button {
    margin-top: 15px;
    padding-top: 18px;
    padding-bottom: 18px;
  }
}

.contacts {
  border-top: 1px solid #74819c;
  grid-column: 2/3;
  display: grid;
  grid-template-columns: 580px 1fr;
}
@media (max-width: 1199px) {
  .contacts {
    grid-template-columns: 1fr 500px 1fr;
  }
}
@media (max-width: 767px) {
  .contacts {
    grid-template-columns: 1fr;
  }
}

.contacts__container {
  padding-top: 55px;
  padding-bottom: 65px;
  padding-left: 120px;
  grid-column: 1/2;
}
@media (max-width: 1199px) {
  .contacts__container {
    padding: 62px 0 64px;
    grid-column: 2/3;
    justify-self: stretch;
  }
}
@media (max-width: 767px) {
  .contacts__container {
    grid-column: 1/2;
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 63px;
  }
}

.contacts h2 {
  font-family: "Alumnisans", "Arial", sans-serif;
  font-size: 54px;
  line-height: 65px;
  font-weight: 600;
  letter-spacing: 1.62px;
  margin-top: 0;
  margin-bottom: 40px;
}
@media (max-width: 1199px) {
  .contacts h2 {
    font-size: 42px;
    line-height: 50px;
    letter-spacing: 1.26px;
  }
}
@media (max-width: 767px) {
  .contacts h2 {
    font-size: 32px;
    line-height: 38px;
    letter-spacing: 0.96px;
  }
}
@media (max-width: 1199px) {
  .contacts h2 {
    font-family: "Alumnisans", "Arial", sans-serif;
    font-size: 42px;
    line-height: 50px;
    font-weight: 600;
    letter-spacing: 1.26px;
    margin-bottom: 30px;
  }
}
@media (max-width: 767px) {
  .contacts h2 {
    font-family: "Alumnisans", "Arial", sans-serif;
    font-size: 32px;
    line-height: 38px;
    font-weight: 600;
    letter-spacing: 0.96px;
    margin-bottom: 35px;
  }
}

.contacts__wrapper {
  width: 100%;
  display: grid;
  position: relative;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto auto;
  grid-auto-rows: auto;
  grid-auto-flow: column;
  row-gap: 20px;
  column-gap: 120px;
  margin-top: 0;
  margin-bottom: 0;
}
@media (max-width: 1199px) {
  .contacts__wrapper {
    grid-template-columns: 310px 230px;
    column-gap: 0;
  }
}
@media (max-width: 767px) {
  .contacts__wrapper {
    display: flex;
    flex-direction: column;
  }
}
.contacts__wrapper::before {
  content: "";
  display: block;
  width: 1px;
  height: 100%;
  position: absolute;
  top: 0;
  left: 50%;
  background-color: #74819c;
}
@media (max-width: 1199px) {
  .contacts__wrapper::before {
    left: 53%;
  }
}
@media (max-width: 767px) {
  .contacts__wrapper::before {
    display: none;
  }
}

.contacts__wrapper dd {
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  margin-left: 0;
}
@media (max-width: 1199px) {
  .contacts__wrapper dd {
    font-size: 18px;
    line-height: 27px;
    font-weight: 400;
  }
}

.contacts__wrapper dt {
  font-size: 16px;
  line-height: 24px;
  font-weight: 700;
  margin-bottom: 0;
}
@media (max-width: 1199px) {
  .contacts__wrapper dt {
    font-size: 20px;
    line-height: 30px;
    font-weight: 700;
    margin-bottom: 5px;
  }
}

.contacts__wrapper a {
  word-spacing: 100vw;
  margin-right: 1rem;
  align-self: flex-start;
}
@media (hover: hover) {
  .contacts__wrapper a:hover:not(.focus-visible) {
    text-decoration: underline;
  }
}

