.promo {
  padding-top: 135px;
  padding-bottom: 125px;

  @include vp-1199 {
    padding-top: 95px;
    padding-bottom: 100px;
  }

  @include vp-767 {
    padding-top: 65px;
    padding-bottom: 65px;
  }
}

.promo__list {
  padding-left: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 40px;
  row-gap: 40px;
  max-width: 1160px;
  margin: 0 auto;

  @include vp-1199 {
    column-gap: 30px;
    row-gap: 30px;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
  }
}

.promo__item {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  margin-top: 0;
  margin-bottom: 0;
  padding: 25px 28px 15px;
  min-height: 211px;

  border: 7px solid;
  border-image-source: $color-button-bg-gradient;
  border-image-slice: 1;

  @include vp-1199 {
    padding: 22px 27px 16px;
    min-height: 210px;
  }

  @include vp-767 {
    align-items: center;
    padding-top: 35px;
    padding-bottom: 27px;
  }
}

.promo h2 {
  @include text-alumnisans-54-65-600;
  text-align: center;
  margin-top: 0;
  margin-bottom: 35px;

  @include vp-1199 {
    margin-bottom: 40px;
  }
}

.promo__item h3 {
  @include text-roboto-26-28-700;
  margin-top: 0;
  margin-bottom: 8px;
}

.promo__item p {
  @include text-roboto-16-24-400;
  margin-top: 0;
  margin-bottom: 0;

  @include vp-1199 {
    margin-bottom: 30px;
  }

  @include vp-767 {
    margin-bottom: 10px;
    text-align: center;
  }
}

.promo__item a {
  @include text-roboto-12-18-700;
  text-transform: uppercase;
  position: relative;
  align-self: start;
  margin-top: auto;
  padding-top: 10px;
  padding-bottom: 13px;

  @include vp-767 {
    align-self: center;
  }

  &:hover {
    opacity: 0.7;
  }

  &:focus {
    padding: 10px;
    margin-left: -11px;
    margin-bottom: 2px;
    border: 1px solid $color-button-border-dark;
    outline: none;

    &:after {
      border-bottom: none;
    }
  }

  &:active {
    border: none;
    margin-bottom: 3px;
    margin-left: 0;
    padding-left: 0;
    padding-right: 0;
    opacity: 1;

    &::after {
      border-bottom: 1px solid $color-button-border-dark;
    }
  }

  &::after {
    content: "";
    position: absolute;
    display: block;
    left: 0;
    width: 100%;
    border-bottom: 1px solid $color-text-base;
  }
}

.promo__item--first {
  display: grid;
  grid-template-columns: 1fr 1fr;

  grid-row: 1 / 3;
  padding: 0;

  border: none;

  @include vp-1199 {
    padding: 0;
    grid-template-columns: 41% 59%;
    grid-column: 1 / 3;
    grid-row: 1 / 2;
  }

  @include vp-767 {
    grid-column: 1 / 2;
    display: flex;
    flex-direction: column;
    padding-top: 0;
    padding-bottom: 0;
  }
}

.promo__description {
  display: flex;
  flex-direction: column;
  background-color: $color-bg-banner;
  color: $color-text-white;

  padding: 42px 44px 35px;

  @include vp-767 {
    width: 100%;
    align-items: center;
    padding-top: 42px;
    padding-bottom: 34px;
  }
}

.promo__img {
  flex-grow: 1;
  height: 100%;
  width: 100%;
}

.promo__img img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  margin: 0;
  padding: 0;
}

.promo__item--first h3 {
  @include text-roboto-28-28-700;
  grid-column: 1 / 2;
  margin-top: 0;
  margin-bottom: 20px;
  width: min-content;

  @include vp-767 {
    text-align: center;
    margin-bottom: 15px;
  }
}

.promo__item--first p {
  @include text-roboto-18-25-400;
  grid-column: 1 / 2;
  margin-top: 0;
  margin-bottom: 0;

  @include vp-767 {
    text-align: center;
    margin-bottom: 20px;
  }
}

.promo__item--first strong {
  @include text-roboto-70-70-700;
}

.promo__item--first a {
  color: $color-text-white;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  position: relative;
  grid-column: 1 / 2;
  margin-top: auto;
  padding-top: 10px;
  padding-bottom: 10px;


  @include vp-767 {
    align-self: center;
  }

  &:hover {
    opacity: 0.8;
  }

  &:focus {
    padding: 10px;
    margin-left: -11px;
    margin-bottom: -1px;
    border: 1px solid $color-button-border-light;
    outline: none;

    &::after {
      height: 0;
    }
  }

  &:active {
    border: none;
    margin-bottom: 0;
    margin-left: 0;
    padding-left: 0;
    padding-right: 0;
    opacity: 1;

    &::after {
      border-color: $color-button-border-light;
    }
  }

  &::after {
    content: "";
    position: absolute;
    display: block;
    top: 30px;
    width: 100%;
    height: 1px;
    border: none;
    background-color: $color-text-white;

    @include vp-1199 {
      top: 25px;
    }
  }
}

.promo__item--disabled a {
  opacity: 0.3;
}
